<template>
  <div class="custom-sub-menu-container">
    <CustomMenu v-show="isFull" :list="routerList" :isGroup="true"></CustomMenu>
    <div
      :class="[
        'main-container',
        isFull ? 'sub-menu-context' : 'sub-menu-context-full',
      ]"
    >
      <router-view></router-view>
    </div>
  </div>
</template>

<script lang="ts" setup>
import { computed } from "vue";
import { authRouter, LocalStorage } from "jz-tool-lib";
import { useVueRouter } from "@assets/useHook";
import ospreyCloudCall from "@/router/menu-router/business-management/osprey-cloud-call";
import CustomMenu from "@/components/common/custom-sub-menu.vue";

defineOptions({
  name: "businessManagement.ospreyCloudCall",
});

const agentInfo = LocalStorage.getItem("bd_agentInfo");
const list = ospreyCloudCall.filter((item) => {
  if (agentInfo.ecSwitch === 0) {
    return item.name !== "businessManagement.ospreyCloudCall.customerPackage";
  }
  return item;
});
const routerList = authRouter(list ?? [], [
  "业务管理",
  "通话质检",
  "呼叫统计",
  "设置中心",
]);
const { route } = useVueRouter();
const isFull = computed(() => {
  // 不需要子菜单
  return !["osprey-cloud-call-report-detail"].includes(route.name);
});
</script>
